.App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.header-menu {
	width: 972px;
	height: 40px;
	display: flex;
	justify-content: space-between;
	margin-bottom: 82px;
	margin-top: 65px;
}

.startup {
	color: var(--colors-white, #fff);
	text-align: center;
	font-size: 24px;
	font-style: normal;
	font-weight: 700;
	line-height: normal;
	letter-spacing: -0.293px;
}

.menu {
	width: 503px;
	height: 40px;
	display: flex;
	gap: 35px;
	align-items: center;
}
.menu-1 {
	display: flex;
	flex-direction: row;
	gap: 35px;
}

.menu-2 {
	width: 120px;
	height: 40px;
	border: 0px;
	border-radius: 100px;
	background-color: #e93a7d;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	font-weight: 500px;
	font-size: 18px;
}

.purchase-btn {
	background-color: #e93a7d;
	border: 0px;
	color: var(--colors-white, #fff);
	text-align: center;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 26px;
	text-decoration: none;
	font-family: DM Sans;
}

.menu-1 a:hover {
	color: gray;
}

.header-btn {
	background-color: #2f1893;
	outline: 0px;
	text-decoration: none;
	color: #fff;
}

.header-component {
	color: var(--colors-white, #fff);

	/* Buttons / Middle */
	background-color: #2f1893;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 26px; /* 144.444% */
}

.content {
	display: flex;
	flex-direction: row;
	gap: 79px;
}

.text-box {
	color: #fff;
	margin-top: 32px;
	width: 427px;
	height: 496px;
	display: flex;
	flex-direction: column;
	gap: 24px;
}

.text-head {
	color: var(--colors-white, #fff);

	/* Heading / H2 Header */
	font-size: 58px;
	font-style: normal;
	font-weight: 700;
	line-height: 70px; /* 120.69% */
	letter-spacing: -1px;
	margin: 0;
}

.text-p {
	color: var(--colors-white, #fff);

	/* Heading / H5 Lead */
	font-size: 22px;
	font-style: normal;
	font-weight: 500;
	line-height: 32px; /* 145.455% */
	margin: 0;
}

.sign-up-form {
	height: 600px;
	width: 470px;
	border: 0px;
	border-radius: 10px;
	background-color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.sign-up-head {
	margin-top: 35px;
	margin-bottom: 34px;
}

.signup-text {
	color: var(--colors-heading, #1e0e62);
	text-align: center;

	/* Heading / H3 Title */
	font-size: 42px;
	font-style: normal;
	font-weight: 700;
	line-height: 52px; /* 123.81% */
	letter-spacing: -0.4px;
	margin: 0;
}

.input-field-1 {
	margin-bottom: 20px;
}

.input-field-2 {
	margin-bottom: 30px;
}

.input-box {
	padding-left: 24px;
	width: 300px;
	height: 45px;
	border-radius: 100px;
	border: 2px solid var(--colors-grey, #ebeaed);

	color: var(--colors-text, rgba(21, 20, 57, 0.4));

	/* Buttons / Middle */
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 26px; /* 144.444% */
}

.check-box {
	margin-right: 50px !important;
	display: flex;
	flex-direction: row;
	gap: 13px;
	align-items: center;
	margin-bottom: 27px;
}
.check-text {
	color: grey;
}

input[type='checkbox'] {
	width: 24px;
	height: 24px;
	accent-color: #25dac5;
	background-color: white;
}

.signin-btn {
	width: 330px;
	height: 50px;
	padding: 0px;
	background-color: #482be7;
	color: var(--colors-white, #fff);
	text-align: center;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 26px;
	border: 1px;
	border-radius: 100px;
}

.or {
	display: flex;
	flex-direction: row;
	align-items: center;
	gap: 8px;
}

.or-text {
	color: var(--colors-text, rgba(21, 20, 57, 0.4));
	text-align: center;

	/* Text / Paragraph */

	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 26px; /* 162.5% */
}

.dash {
	background-color: var(--colors-grey, #ebeaed);
	width: 140px;
	height: 1.5px;
}

.twitter-box {
	margin-bottom: 30px;
}

.twitter-btn {
	width: 330px;
	height: 50px;
	padding: 0px;
	background: var(--socials-twitter, #1da1f2);
	color: var(--colors-white, #fff);
	text-align: center;
	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 26px;
	border: 1px;
	border-radius: 100px;
}

.another-account {
	display: flex;
	flex-direction: row;
	gap: 10px;
	align-items: center;
}
.another-account-text {
	color: var(--colors-text, rgba(21, 20, 57, 0.4));
	text-align: center;

	/* Text / Article */

	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 28px; /* 155.556% */
}

.another-account-signin {
	text-decoration: none;
	color: var(--action-primary, #25dac5);

	/* Text / Article */

	font-size: 18px;
	font-style: normal;
	font-weight: 500;
	line-height: 28px; /* 155.556% */
}
